import { useEffect } from 'react'
import { useRef } from 'react'
import { useMemo, useState } from 'react'
import {Circle} from 'react-konva'
import useSound from 'use-sound';
import chipSfx from '../sounds/chip.mp3';

const ChipComp = ({x, y,num, fill, stroke}) => {

  const [play] = useSound(chipSfx, { volume: 0.02 });
  const [coords, setCoords] = useState([0, 0])
  const [mounted, setMounted] = useState(false)
  const circle = useRef('')

  useEffect(() => {
    setCoords([x, y])
    setMounted(true)
  }, []);

  const chipAnimation = () => {
    circle.current.to({
      x:x,
      Y:y,
      duration:0.5,
    })
  }

  const animation = () => {
    chipAnimation();
    play()
    setCoords([x, y]);
  }
 
  useMemo(() => {
    if(mounted){
      animation()
    }
    }, [x, y])

  return (
        <Circle 
        ref={circle}
        x={coords[0]} y={coords[1]} 
        radius={30} 
        fill={fill} 
        stroke={stroke}
        />
  );
}

export default ChipComp;