import board from '../models/Board'

const ModalWin = ({hidden}) => {

    return(
        <div className={hidden ? `hidden` : `modal-container`}>
            <div className="modalw">
                <div className="modal-body">
                    <div className="modal-text-win">
                         {board.winner}!
                    </div>
                </div>                
            </div>
        </div>
    )
}

export default ModalWin;