import { Stage, Layer, Image as KonvaImage} from 'react-konva'
import fieldimg from "../images/nardi.png"
import ChipComp from './ChipComp'
import game from "../store/gameState"
import board from '../models/Board'
import {observer} from "mobx-react-lite"
import React, { useMemo, useState} from 'react'
import LineComp from './LineComp'
import {useParams} from "react-router-dom"
import kazino from "../sounds/kazino.mp3"
import useSound from 'use-sound';
import ModalMenu from './ModalMenu'
import ModalWin from './ModalWin'

const Field = observer(() => {
  const img = new Image()
  img.src = fieldimg
  const params = useParams()

  const [play] = useSound(kazino, { volume: 0.20 });
  const [winModal, setWinModal] = useState(true);

  function restart(){
    board.init()
    board.linesHeightSort()
  }

  const handleConnection = () =>{
    const socket = new WebSocket("wss://billowy-chocolate-chord.glitch.me")
    // wss://billowy-chocolate-chord.glitch.me
    // ws://localhost:5000
    board.setSocket(socket)
    board.setSessionId(params.id)
    socket.onopen = () => {
      socket.send(JSON.stringify({
        id:params.id,
        username: board.username,
        method: "connection"
      }))
    }
    socket.onmessage = (event) => {
      let msg = JSON.parse(event.data);
      switch(msg.method){
        case "connection":
          board.connected = true;
          if(msg.pwhite){
            game.setWhiteName(msg.pwhite);
          }
          if(msg.pblack){
            game.setBlackName(msg.pblack);
          }
          break
        case "sit":
          handleSit(msg);
          break
        case "start":
          handeStart(msg);
          break
        case "move":
          handleMove(msg);
          break
        case "changeturn":
          handleChangeTurn(msg);
          break
        case "win":
          handleWin(msg);
          break
      }
    }
  }

  const handeStart = (msg) =>{
    game.setTurn(msg.turn);
    board.setBones(msg.bones);
    game.setDices(msg.bones);
    play()
    board.setCanpickhead();
  }

  const handleWin = (msg) =>{
    board.setWinner(msg.username)
    setWinModal(false)
  }

  const handleChangeTurn = (msg) =>{
    board.setBones(msg.bones);
    game.setDices(msg.bones);
    game.setTurn(msg.turn);
  }

  const handleMove = (msg) => {
    board.changeChipLine(msg)
  }

  const handleSit = (msg) => {
    if(msg.color==="white"){
      game.white_name = msg.username;
    }
    if(msg.color==="black"){
      game.black_name = msg.username;
    }
    if(board.username == msg.username && board.chips.length == 0){
      game.pc = msg.color;
      restart()
    }
  }

  useMemo(()=>{
    board.linesHeightSort()
  })

  return (
      <React.Fragment>
        <ModalMenu onClick={handleConnection}/>
        <ModalWin hidden={winModal}/>
        <Stage width={1000} height={600}>
          <Layer>
            <KonvaImage image={img} />  
          </Layer>
          <Layer>
            {board.chips.map((chip, index)=>
                <ChipComp
                key={chip.num}
                x={chip.x}
                y={chip.y}
                num={chip.num}
                fill={chip.fill}
                stroke={chip.stroke}
                />
            )}
          </Layer>
          <Layer>
            {board.lines.map((line, index)=>
                <LineComp
                key={index}
                num={line.num}
                x={line.x}
                y={line.y}
                width={line.width}
                height={line.height}
                fill={line.fill}
                stroke={line.stroke}
                />
            )}
          </Layer>
        </Stage>
      </React.Fragment> 
  );
})

export default Field;