import {makeAutoObservable} from "mobx"

class Game{
    white_name = ""
    black_name = ""
    turn = ""
    pc = ""
    dices = []
    constructor(){
        makeAutoObservable(this)
    }
    setWhiteName(name){
        this.white_name = name
    }
    setBlackName(name){
        this.black_name = name
    }
    setTurn(turn){
        this.turn = turn
    }
    setDices(dices){
        this.dices = dices
    }
}

export default new Game()
